header {

    background-color: #fff;
}

.header {

    padding: 15px 0;

    @media screen and (min-width: $w576) {
        padding: 20px 0;
    }

    &.is-fixed {
        position: fixed;
        background-color: #fff;
        z-index: 10;
        left: 0;
        right: 0;
        top: 0;
        box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.08);

        @media screen and (min-width: $w1200) {
            & .header {
                &__catalog {
                    display: block;
                }
            }
        }

        @media screen and (min-width: $w576) {
            & .header {



                &__msgr {
                    display: block;
                }

                &__logo {
                    & .bgimage {
                        width: 28px;
                        height: 28px;
                        background-size: cover;
                    }
                }


            }
        }

        & .icon-kp {
            display: list-item;
        }
    }

    &__catalog {
        display: none;
        font-size: 15px;
    }

    &__msgr {

        display: none;

        & ul {
            display: flex;
            align-items: center;
            gap: 10px;

            & li {

                display: flex;
                align-items: center;

                & a {
                    display: flex;
                    align-items: center;
                    width: 40px;
                    height: 40px;
                    justify-content: center;
                    background-color: #eee;
                    border-radius: 50%;
                    transition: 0.3s ease;

                    &:hover {
                        background-color: #cee0e7;
                        cursor: pointer;
                    }
                }


                & span {
                    width: 20px;
                    height: 20px;
                    display: inline-block;
                    filter: invert(1);
                    opacity: 0.95;


                }
            }
        }
    }

    &__wrp {
        display: flex;
        align-items: center;
        gap: 18px;

        @media screen and (min-width: $w1200) {
            gap: 36px;
        }
    }

    &__find {
        flex-grow: 1;

        & input {
            width: 100%;
            border-radius: 50px;
            background: #F3F3F3;
            border: none;
            height: 42px;
            padding: 0 2em;
            padding-left: 50px;
            background-image: url(data:image/svg+xml;base64,);
            background-position: 15px center;
            background-size: 20px;
            background-repeat: no-repeat;
            font-size: 14px;
            transition: 0.3s ease;

            @media screen and (max-width: $w760) {
                display: none;
            }

        }

        & .find-icon {

            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            margin-left: auto;

            @media screen and (min-width: $w760) {
                display: none;
            }
        }

    }

    &__logo {

        & a {
            display: flex;
            align-items: center;
        }

        & .bgimage {
            display: inline-block;
            background-size: contain;
            background-position: left center;
            background-repeat: no-repeat;

            width: 236px;
            height: 25px;

            @media screen and (min-width: $w1200) {
                width: 260px;
                height: 28px;
            }

            @media screen and (min-width: $w576) {
                width: 203px;
                height: 22px;
            }
        }
    }

    &__actions {

        display: none;

        @media screen and (min-width: $w576) {
            display: block;
        }

        & ul {
            display: flex;
            align-items: center;
            gap: 6px;


            @media screen and (min-width: $w992) {
                margin-top: -7px;
            }

            & li {
                & a {
                    width: 40px;
                    height: 40px;
                    display: flex;
                    align-items: center;

                    transition: 0.4s ease;

                    & svg {
                        fill: var(--primary);

                        transition: 0.3s ease;
                    }

                    &.ic_heart {
                        & svg {
                            stroke: var(--primary);
                        }

                        &:hover {
                            background-color: #f5f5f5;
                            border-radius: 6px;

                            & svg {
                                fill: var(--accent);
                                stroke: var(--accent);
                            }
                        }
                    }

                    &:hover {
                        background-color: #f5f5f5;
                        border-radius: 6px;

                        & svg {
                            fill: var(--accent);
                        }
                    }
                }

                &.icon-kp {
                    display: none;
                }
            }
        }
    }

    &__burger {
        display: none;

        @media screen and (max-width: $w576) {
            display: block;
        }
    }

    &__contacts {

        display: none;

        @media screen and (min-width: 991px) {
            display: block;
        }
    }


}

.header-contact {
    &__value {
        & a {
            font-weight: 800;
            font-size: 21px;
            line-height: 100%;

            &:hover {
                color: var(--accent);
            }
        }
    }

    &__label {
        & a {
            font-size: 13px;
            font-weight: 600;
            display: flex;
            align-items: center;
            line-height: 100%;

            &:hover {
                color: var(--accent);
            }
        }

        & .copy-fields {
            display: flex;
            align-items: center;
            height: 20px;
        }

        & svg {
            width: 20px;
            height: 20px;
            fill: var(--accent)
        }
    }
}


@import 'header-menu.scss';
@import 'header-top.scss';