:root {
    --gap-between-blocks: 50px;
    --screen-padding: 20px;
    --h1-font-size: 30px;
    --h2-font-size: 24px;
    --h3-font-size: 17px;
    --h4-font-size: 15px;
    --link_font_size: 15px;
    --body_font_size: 15px;
    --font-montserrat: Montserrat;
}

/* colors light */

:root {
    --primary: #000;
    --accent: #0090c3;
    --accent-hover: #41BFF1;
    --secondaty: #0009;
    --disabled: #0006;
    --critical: #ff3232;
    --caution: #ffe816;
    --success: #3df55b;
    --inverse: #fff;
    --Text-inverse: #FFF;
    --dark: #2F3030;
    --black: #000
}

@media screen and (min-width: $w992) {
    :root {
        --gap-between-blocks: 100px;
        --screen-padding: 40px;
        --h1-font-size: 40px;
        --h2-font-size: 30px;
        --h3-font-size: 24px;
        --h4-font-size: 18px;
        --link_font_size: 15px;
        --body_font_size: 15px;
    }
}

@media screen and (min-width: $w576) {
    :root {
        --gap-between-blocks: 80px;
        --screen-padding: 30px;
        --h1-font-size: 36px;
        --h2-font-size: 27px;
        --h3-font-size: 17px;
        --h4-font-size: 16px;
        --link_font_size: 15px;
        --body_font_size: 15px;
    }
}

