.lazyload {
    opacity: 0;
}

.lazyloaded {
    opacity: 1;
    transition: opacity 0.3s ease;
}

.overflow--hidden {
    overflow: hidden !important;
}

.overflow--hidden-fixed {
    overflow: hidden !important;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.overflow--hidden-scrollBar {
    overflow-x: hidden !important;
    overflow-y: scroll !important;
}

.safari-fixed {
    height: calc(100vh - 1px);
    overflow: hidden !important;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}


.table-responsive {
    overflow-x: auto;

    & table {
        min-width: auto;
    }
}

.lazyload {
    opacity: 0;
}

.lazyloaded {
    opacity: 1;
    transition: opacity 0.5s ease;
}